import * as types from './ActionTypes';

export const increment = () => ({
    type: types.INCREMENT
});


export const calculatePriceForIncome = () => ({
    type: types.CALCULATE_PRICE_FOR_INCOME
});

export const onChangeCountyCode = (countryCode) => ({
    countryCode: countryCode,
    type: types.ON_CHANGE_COUNTRY_CODE
});

export const onChangeCurrencyRate = (currencyRate) => ({
    currencyRate: currencyRate,
    type: types.ON_CHANGE_CURRENCY_RATE
});

export const onChangeProductPriceInKrw = (productPriceInKrw) => ({
    productPriceInKrw: productPriceInKrw,
    type: types.ON_CHANGE_PRODUCT_PRICE_IN_KRW
});

export const onChangeAdditionalCost = (additionalCost) => ({
    additionalCost: additionalCost,
    type: types.ON_CHANGE_ADDITIONAL_COST
});

export const onChangeProductWeight = (productWeight) => ({
    productWeight: productWeight,
    type: types.ON_CHANGE_PRODUCT_WEIGHT
});

export const onChangePgFeeRate = (pgFeeRate) => ({
    pgFeeRate: pgFeeRate,
    type: types.ON_CHANGE_PG_FEE_RATE
});

export const onChangeSalesFeeRate = (salesFeeRate) => ({
    salesFeeRate: salesFeeRate,
    type: types.ON_CHANGE_SALES_FEE_RATE
});

export const onChangePayoneerFeeRate = (payoneerFeeRate) => ({
    payoneerFeeRate: payoneerFeeRate,
    type: types.ON_CHANGE_PAYONEER_FEE_RATE
});

export const onChangeFspFeeRate = (fspFeeRate) => ({
    fspFeeRate: fspFeeRate,
    type: types.ON_CHANGE_FSP_FEE_RATE
});

export const onChangeDiscountRate = (discountRate) => ({
    discountRate: discountRate,
    type: types.ON_CHANGE_DISCOUNT_RATE
});
export const onChangeProceForIncome = (priceForIncome) => ({
    priceForIncome: priceForIncome,
    type: types.ON_CHANGE_PRICE_FOR_INCOME
});