import PresentationalThirdSection from '../components/PresentationalThirdSection';
import * as actions from '../actions';
import { connect } from 'react-redux';


const mapStateToProps = (state) => ({
    localCurrency: state.config.localCurrency,
    priceForIncome: state.thirdSection.priceForIncome,
    marginRate: state.thirdSection.marginRate,
    marginRatePercentage: state.thirdSection.marginRate * 100,
    isMinusMargin: state.thirdSection.marginRate < 0 ? true : false,
    margin: state.thirdSection.margin,
    discountRate: state.thirdSection.discountRate,
    discountRatePercentage: state.thirdSection.discountRate * 100,
    priceForRegistration: state.thirdSection.priceForRegistration

})

const mapDispatchToProps = (dispatch) => ({
    onChangeDiscountRate: (e) => dispatch(actions.onChangeDiscountRate(e.target.value)),
    onChangeProceForIncome: (e) => dispatch(actions.onChangeProceForIncome(e.target.value))
});

const ThirdSectionContainer = connect(
    mapStateToProps,
    mapDispatchToProps
)(PresentationalThirdSection);

export default ThirdSectionContainer;