import * as types from '../actions/ActionTypes';
import * as rates from "../resources/js/utils/rates";
import * as config from "../resources/js/utils/config";

// 액션 Type의 초기설정 
const initialState = {
    number: 1,
    config: {
        "countryCode": "SG",
        "localCurrency": "SGD",
        "rebate": 1.99,
        "rebateStandard": 25
    },
    firstSection: {
        productPriceInKrw: 0,
        currencyRate: 0,
        additionalCost: 0,
        productWeight: 10,
        pgFeeRate: 2.0,
        salesFeeRate: 3.0,
        payoneerFeeRate: 1.2,
        fspFeeRate: 4.82
    },
    secondSection: {
        productPriceInLocalCurrency: 0,
        additionalCostInLocalCurrency: 0,
        shippingCostInLocalCurrency: 0,
        shippingCostDetail: {
            costBeforeRebate: 0,
            payByBuyer: 1.99,
            payByShopee: 0,
            rebate: 0
        },
        totalFeeInLocalCurrency: 0,
        totalFeeDetail: {
            pgFee: 0,
            salesFee: 0,
            payoneerFee: 0,
            fspFee: 0
        },
        totalInLocalCurrency: 0
    },
    thirdSection: {
        priceForIncome: 0,
        marginRate: 0,
        margin: 0,
        discountRate: 0.3,
        priceForRegistration: 0
    }
};

// 리듀서 함수는 state와 action을 파라미터로 받습니다. 
// state로 넘어오는 값이 없을 경우 initialState값이 사용됩니다.
function reducer(state = initialState, action) {
    switch (action.type) {
        case types.INCREMENT:
            return {
                ...state,
                number: state.number + 1
            };

        case types.CALCULATE_PRICE_FOR_INCOME:
            return fnCaculatePrice(state)

        case types.ON_CHANGE_COUNTRY_CODE:
            const countryConfig = fnGetCountryConfig(action.countryCode)
            state.config = countryConfig
            return {
                ...state
            };

        case types.ON_CHANGE_CURRENCY_RATE:
            state.firstSection.currencyRate = parseFloat(action.currencyRate)
            return {
                ...state
            };

        case types.ON_CHANGE_PRODUCT_PRICE_IN_KRW:
            state.firstSection.productPriceInKrw = parseFloat(action.productPriceInKrw)
            return {
                ...state
            };

        case types.ON_CHANGE_ADDITIONAL_COST:
            state.firstSection.additionalCost = parseFloat(action.additionalCost)
            return {
                ...state
            };

        case types.ON_CHANGE_PRODUCT_WEIGHT:
            state.firstSection.productWeight = parseFloat(action.productWeight)
            return {
                ...state
            };

        case types.ON_CHANGE_PG_FEE_RATE:
            state.firstSection.pgFeeRate = parseFloat(action.pgFeeRate)
            return {
                ...state
            };

        case types.ON_CHANGE_SALES_FEE_RATE:
            state.firstSection.salesFeeRate = parseFloat(action.salesFeeRate)
            return {
                ...state
            };

        case types.ON_CHANGE_PAYONEER_FEE_RATE:
            state.firstSection.payoneerFeeRate = parseFloat(action.payoneerFeeRate)
            return {
                ...state
            };

        case types.ON_CHANGE_FSP_FEE_RATE:
            state.firstSection.fspFeeRate = parseFloat(action.fspFeeRate)
            return {
                ...state
            };

        case types.ON_CHANGE_DISCOUNT_RATE:
            state.thirdSection.discountRate = parseFloat(action.discountRate / 100).toFixed(2)
            console.log(action)
            return fnCaculatePrice(state);

        case types.ON_CHANGE_PRICE_FOR_INCOME:
            state.thirdSection.priceForIncome = parseFloat(action.priceForIncome).toFixed(2)
            return fnCaculatePrice(state);

        default:
            return state;
    }
};

const fnGetLatestData = (data) => {
    let today = new Date();
    let todayDateOnly = [today.getFullYear(), String(today.getMonth() + 1).padStart(2, '0'), String(today.getDate()).padStart(2, '0')].join("")
    let selectedData = null

    const keys = Object.keys(data);
    keys.sort()
    keys.forEach(function (d) {
        if (todayDateOnly < d) {
            return;
        }
        selectedData = data[d];
    });

    return selectedData
}


const fnGetShippingRateTable = (countryCode) => {
    switch (countryCode.toUpperCase()) {
        case "SG":
            return fnGetLatestData(rates.sg_rate)
        case "MY":
            return fnGetLatestData(rates.my_rate)
        default:
            return null;

    }
}

const fnGetCountryConfig = (countryCode) => {
    switch (countryCode.toUpperCase()) {
        case "SG":
            return fnGetLatestData(config.sg_config)
        case "MY":
            return fnGetLatestData(config.my_config)
        default:
            return null;
    }
}

const fnCaculatePrice = (state) => {
    const rateTable = fnGetShippingRateTable(state.config.countryCode);
    const countryConfig = fnGetCountryConfig(state.config.countryCode);
    const productPriceInLocalCurrency = parseFloat((state.firstSection.productPriceInKrw / state.firstSection.currencyRate).toFixed(2));
    const additionalCostInLocalCurrency = parseFloat((state.firstSection.additionalCost / state.firstSection.currencyRate).toFixed(2));
    const costBeforeRebate = rateTable[state.firstSection.productWeight.toString()].cost;
    const payByBuyer = productPriceInLocalCurrency >= countryConfig.rebateStandard ? countryConfig.rebate : 0;
    const payByShopee = productPriceInLocalCurrency < countryConfig.rebateStandard ? countryConfig.rebate : 0;

    const rebate = payByBuyer + payByShopee
    const shippingCostInLocalCurrency = parseFloat((costBeforeRebate - rebate).toFixed(2));
    const pgFee = parseFloat(state.thirdSection.priceForIncome * (state.firstSection.pgFeeRate / 100).toFixed(2));
    const salesFee = parseFloat(state.thirdSection.priceForIncome * (state.firstSection.salesFeeRate / 100).toFixed(2));
    const payoneerFee = parseFloat(state.thirdSection.priceForIncome * (state.firstSection.payoneerFeeRate / 100).toFixed(2));
    const fspFee = parseFloat(state.thirdSection.priceForIncome * (state.firstSection.fspFeeRate / 100).toFixed(2));
    const totalFeeInLocalCurrency = pgFee + salesFee + payoneerFee + fspFee
    const totalInLocalCurrency = productPriceInLocalCurrency + additionalCostInLocalCurrency + shippingCostInLocalCurrency + totalFeeInLocalCurrency
    const result = {
        firstSection: {
            productPriceInKrw: state.firstSection.productPriceInKrw,
            currencyRate: state.firstSection.currencyRate,
            additionalCost: state.firstSection.additionalCost,
            productWeight: state.firstSection.productWeight,
            pgFeeRate: state.firstSection.pgFeeRate,
            salesFeeRate: state.firstSection.salesFeeRate,
            payoneerFeeRate: state.firstSection.payoneerFeeRate,
            fspFeeRate: state.firstSection.fspFeeRate
        },
        secondSection: {
            productPriceInLocalCurrency: productPriceInLocalCurrency,
            additionalCostInLocalCurrency: additionalCostInLocalCurrency,
            shippingCostInLocalCurrency: shippingCostInLocalCurrency,
            shippingCostDetail: {
                costBeforeRebate: costBeforeRebate,
                payByBuyer: payByBuyer,
                payByShopee: payByShopee,
                rebate: rebate
            },
            totalFeeInLocalCurrency: totalFeeInLocalCurrency,
            totalFeeDetail: {
                pgFee: pgFee,
                salesFee: salesFee,
                payoneerFee: payoneerFee,
                fspFee: fspFee
            },
            totalInLocalCurrency: totalInLocalCurrency
        },
        thirdSection: {
            priceForIncome: state.thirdSection.priceForIncome,
            marginRate: state.thirdSection.priceForIncome <= 0 ? -1 : parseFloat((state.thirdSection.priceForIncome - totalInLocalCurrency) / state.thirdSection.priceForIncome).toFixed(2),
            margin: parseFloat(state.thirdSection.priceForIncome - totalInLocalCurrency).toFixed(2),
            discountRate: parseFloat(state.thirdSection.discountRate),
            priceForRegistration: parseFloat(state.thirdSection.priceForIncome / (1 - state.thirdSection.discountRate)).toFixed(2)
        }
    }

    return {
        ...state,
        firstSection: result.firstSection,
        secondSection: result.secondSection,
        thirdSection: result.thirdSection
    }
}

export default reducer;