

import React, { useEffect } from 'react';
import { OverlayTrigger, Tooltip } from 'react-bootstrap';
import PropTypes from 'prop-types'; //props 기본 설정
import ReactGA from 'react-ga'
const PresentationalFirstSection = ({
    localCurrency,
    currencyRate,
    productPriceInKrw,
    additionalCost,
    productWeight,
    pgFeeRate,
    salesFeeRate,
    payoneerFeeRate,
    fspFeeRate,
    onChangeCountryCode,
    onChangeCurrencyRate,
    onChangeProductPriceInKrw,
    onChangeAdditionalCost,
    onChangeProductWeight,
    onChangePgFeeRate,
    onChangeSalesFeeRate,
    onChangePayoneerFeeRate,
    onChangeFspFeeRate,
    onCalculatePriceForIncome,
}) => {
    useEffect(() => {
        ReactGA.initialize("G-QZ96X68ZSY");
        ReactGA.set({ page: window.location.pathname });
        ReactGA.pageview(window.location.pathname + window.location.search);
    }, []);

    return (
        <section className="page-section masthead portfolio" id="userInputSection">
            <div className="container">
                <h2 className="page-section-heading text-uppercase text-secondary mb-0">1. 배송 및 수수료 정보 입력</h2>
                <div className="divider-custom">
                    <div className="divider-custom-line" style={{ maxWidth: "initial" }}></div>
                </div>
                <h3 className="text-secondary content-in-container">배송정보</h3>
                <div className="row justify-content-between">
                    <div className="col-lg-6 col-md-12 content-in-container">
                        <div className="form-group">
                            <label htmlFor="country">국가</label>
                            <OverlayTrigger
                                key="country-info"
                                placement="top"
                                overlay={
                                    <Tooltip id="country-info">
                                        상품판매 대상 국가(현재 싱가포르와 말레이시아만 가능)
                                    </Tooltip>
                                }
                            >
                                <button type="button" className="btn btn-outline-secondary btn-circle btn-sm"><i className="fas fa-info"></i></button>
                            </OverlayTrigger>
                            <select title="국가를 선택하세요." onChange={onChangeCountryCode} className="form-control selectpicker show-tick" id="country">
                                <option value="SG"
                                    data-icon="<img className='selectbox-image' src='images/flags/singapore.png' /><span className='badge badge-success text-dark'>싱가포르</span>">
                                    싱가포르</option>
                                <option value="MY"
                                    data-content="<img className='selectbox-image' src='images/flags/malaysia.png'><span className='text-dark'>말레이시아</span>">
                                    말레이시아</option>
                                <option disabled
                                    data-content="<img className='selectbox-image' src='images/flags/indonesia.png'><span className='text-dark'>인도네시아(준비중)</span>">
                                    인도네시아</option>
                                <option disabled
                                    data-content="<img className='selectbox-image' src='images/flags/taiwan.png'><span className='text-dark'>대만(준비중)</span>">
                                    대만 </option>
                                <option disabled
                                    data-content="<img className='selectbox-image' src='images/flags/thailand.png'><span className='text-dark'>태국(준비중)</span>">
                                    태국 </option>
                                <option disabled
                                    data-content="<img className='selectbox-image' src='images/flags/vietnam.png'><span className='text-dark'>베트남(준비중)</span>">
                                    베트남 </option>
                                <option disabled
                                    data-content="<img className='selectbox-image' src='images/flags/philippines.png'><span className='text-dark'>필리핀(준비중)</span>">
                                    필리핀 </option>
                            </select>

                        </div>
                    </div>
                    <div className="col-lg-5 col-md-12 content-in-container">
                        <div className="form-group">
                            <label htmlFor="currency">환율</label>
                            <OverlayTrigger
                                key="currency-info"
                                placement="top"
                                overlay={
                                    <Tooltip id="currency-info">
                                        계산에 적용할 환율. 환율을 낮게 적용 할 수록 판매가격은 높아지지만 환차에 의한 손해를 볼 가능성은 낮아짐.
                                    </Tooltip>
                                }
                            >
                                <button type="button" className="btn btn-outline-secondary btn-circle btn-sm"><i className="fas fa-info"></i></button>
                            </OverlayTrigger>
                            <div className="input-group mb-3">
                                <input defaultValue={currencyRate} onChange={onChangeCurrencyRate} type="number" min="0" className="form-control" placeholder="환율을 입력하세요." aria-label="환율을 입력하세요" aria-describedby="currency-addon" id="currency" />
                                <div className="input-group-append">
                                    <span className="input-group-text" type="number" min="0" id="currency-addon">원(KRW)</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-6 col-md-12 content-in-container">
                        <div className="form-group">
                            <label htmlFor="productPrice">상품 가격(원화)</label>
                            <OverlayTrigger
                                key="productPrice-info"
                                placement="top"
                                overlay={
                                    <Tooltip id="productPrice-info">
                                        상품을 실제 구매한 가격
                                    </Tooltip>
                                }
                            >
                                <button type="button" className="btn btn-outline-secondary btn-circle btn-sm"><i className="fas fa-info"></i></button>
                            </OverlayTrigger>
                            <div className="input-group mb-3">
                                <input defaultValue={productPriceInKrw} onChange={onChangeProductPriceInKrw} type="number" min="0" step="10" className="form-control" placeholder="상품 가격을 입력하세요." aria-label="상품 가격을 입력하세요" aria-describedby="productPrice-addon" id="productPrice" />
                                <div className="input-group-append">
                                    <span className="input-group-text" id="productPrice-addon">원(KRW)</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-5 col-md-12 content-in-container">
                        <div className="form-group">
                            <label htmlFor="addtionalCost">기타 추가 비용</label>
                            <OverlayTrigger
                                key="addtionalCost-info"
                                placement="top"
                                overlay={
                                    <Tooltip id="addtionalCost-info">
                                        상품 구매가격 이외 발생한 비용 - 배송비, 배대지 사용비 등
                                    </Tooltip>
                                }
                            >
                                <button type="button" className="btn btn-outline-secondary btn-circle btn-sm"><i className="fas fa-info"></i></button>
                            </OverlayTrigger>
                            <div className="input-group mb-3">
                                <input defaultValue={additionalCost} onChange={onChangeAdditionalCost} type="number" min="0" step="10" className="form-control" placeholder="국내 배송비, 배대지 이용비, 포장비 등을 입력하세요." aria-label="국내 배송비, 배대지 이용비, 포장비 등을 입력하세요" aria-describedby="addtionalCost-addon" id="addtionalCost" />
                                <div className="input-group-append">
                                    <span className="input-group-text" id="addtionalCost-addon">원(KRW)</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-6 col-md-12 content-in-container">
                        <div className="form-group">
                            <label htmlFor="productWeight">제품 무게</label>
                            <OverlayTrigger
                                key="productWeight-info"
                                placement="top"
                                overlay={
                                    <Tooltip id="productWeight-info">
                                        배송을 위한 포장을 마친 상태의 제품 무게로써 10g 단위로 입력 가능
                                    </Tooltip>
                                }
                            >
                                <button type="button" className="btn btn-outline-secondary btn-circle btn-sm"><i className="fas fa-info"></i></button>
                            </OverlayTrigger>
                            <div className="input-group mb-3">
                                <input defaultValue={productWeight} onChange={onChangeProductWeight} type="number" min="0" max="30000" step="10" className="form-control" placeholder="제품의 무게(10g단위)를 입력하세요." aria-label="제품의 무게(10g단위)를 입력하세요." aria-describedby="productWeight-addon" id="productWeight" />
                                <div className="input-group-append">
                                    <span className="input-group-text" id="productWeight-addon">그램(g)</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <h3 className="mt-5 text-secondary content-in-container">내 수수료 정보</h3>
                <div className="row justify-content-between">
                    <div className="col-lg-6 col-md-12 content-in-container">
                        <div className="form-group">
                            <label htmlFor="country">해외 PG 수수료</label>
                            <OverlayTrigger
                                key="productWeight-info"
                                placement="top"
                                overlay={
                                    <Tooltip id="productWeight-info">
                                        쇼피에서 고객이 상품을 구매한 경우 발생하는 PG(결재대행) 수수료
                                    </Tooltip>
                                }
                            >
                                <button type="button" className="btn btn-outline-secondary btn-circle btn-sm"><i className="fas fa-info"></i></button>
                            </OverlayTrigger>
                            <div className="input-group mb-3">
                                <input defaultValue={pgFeeRate} onChange={onChangePgFeeRate} type="number" min="0" max="100" step="0.01" className="form-control" placeholder="%는 제외하고 입력하세요." aria-label="%는 제외하고 입력하세요." aria-describedby="transactionFee-addon" id="transactionFee" />
                                <div className="input-group-append">
                                    <span className="input-group-text" id="transactionFee-addon">%</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-5 col-md-12 content-in-container">
                        <div className="form-group">
                            <label htmlFor="country">판매 수수료</label>
                            <OverlayTrigger
                                key="productWeight-info" placement="top" overlay={
                                    <Tooltip id="productWeight-info">
                                        쇼피에서 고객이 상품을 구매한 경우 발생하는 판매 수수료
                                    </Tooltip>
                                }>
                                <button type="button" className="btn btn-outline-secondary btn-circle btn-sm"><i className="fas fa-info"></i></button>
                            </OverlayTrigger>
                            <div className="input-group mb-3">
                                <input defaultValue={salesFeeRate} onChange={onChangeSalesFeeRate} type="number" min="0" max="100" step="0.01" className="form-control" placeholder="%는 제외하고 입력하세요." aria-label="%는 제외하고 입력하세요." aria-describedby="salesFee-addon" id="salesFee" />
                                <div className="input-group-append">
                                    <span className="input-group-text" id="salesFee-addon">%</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-6 col-md-12 content-in-container">
                        <div className="form-group">
                            <label htmlFor="country">페이오니아 수수료</label>
                            <OverlayTrigger
                                key="productWeight-info" placement="top" overlay={
                                    <Tooltip id="productWeight-info">
                                        판매 대금을 페이오니아에서 인출할 때 발생하는 수수료
                                    </Tooltip>
                                }>
                                <button type="button" className="btn btn-outline-secondary btn-circle btn-sm"><i className="fas fa-info"></i></button>
                            </OverlayTrigger>
                            <div className="input-group mb-3">
                                <input defaultValue={payoneerFeeRate} onChange={onChangePayoneerFeeRate} type="number" min="0" max="100" step="0.01" className="form-control"
                                    placeholder="%는 제외하고 입력하세요." aria-label="%는 제외하고 입력하세요." aria-describedby="payoniaFee-addon" id="payoniaFee" />
                                <div className="input-group-append">
                                    <span className="input-group-text" id="payoniaFee-addon">%</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-5 col-md-12 content-in-container">
                        <div className="form-group">
                            <label htmlFor="country">FSP 수수료</label>
                            <OverlayTrigger
                                key="productWeight-info" placement="top" overlay={
                                    <Tooltip id="productWeight-info">
                                        Free Shipping / Coin Cashback 프로모션을 진행하는 경우 발생하는 수수료
                                    </Tooltip>
                                }>
                                <button type="button" className="btn btn-outline-secondary btn-circle btn-sm"><i className="fas fa-info"></i></button>
                            </OverlayTrigger>
                            <div className="input-group mb-3">
                                <input defaultValue={fspFeeRate} onChange={onChangeFspFeeRate} type="number" min="0" max="100" step="0.01" className="form-control" placeholder="%는 제외하고 입력하세요." aria-label="%는 제외하고 입력하세요." aria-describedby="fspFee-addon" id="fspFee" />
                                <div className="input-group-append">
                                    <span className="input-group-text" id="fspFee-addon">%</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="mt-5 pr-6 col-12 text-right content-in-container">
                        <button type="button" className="btn btn-primary" onClick={onCalculatePriceForIncome}>계산하기</button>
                    </div>
                </div>
            </div>
        </section >
    );
};



PresentationalFirstSection.propTypes = {
    currencyRate: PropTypes.number,
    productPriceWithLocalCurrency: PropTypes.number,
    additionalCost: PropTypes.number,
    productWeight: PropTypes.number,
    pgFee: PropTypes.number,
    salesFee: PropTypes.number,
    payoneerFee: PropTypes.number,
    fspFee: PropTypes.number
};

export default PresentationalFirstSection;




