import PresentationalSecondSection from '../components/PresentationalSecondSection';
import { connect } from 'react-redux';

// store 안의 state 값을 props 로 연결해줍니다.
const mapStateToProps = (state) => ({
    localCurrency: state.config.localCurrency,
    productPriceInLocalCurrency: state.secondSection.productPriceInLocalCurrency,
    additionalCostInLocalCurrency: state.secondSection.additionalCostInLocalCurrency,
    shippingCostInLocalCurrency: state.secondSection.shippingCostInLocalCurrency,
    costBeforeRebate: state.secondSection.shippingCostDetail.costBeforeRebate,
    payByBuyer: state.secondSection.shippingCostDetail.payByBuyer,
    payByShopee: state.secondSection.shippingCostDetail.payByShopee,
    rebate: state.secondSection.shippingCostDetail.rebate,
    totalFeeInLocalCurrency: state.secondSection.totalFeeInLocalCurrency,
    pgFee: state.secondSection.totalFeeDetail.pgFee,
    salesFee: state.secondSection.totalFeeDetail.salesFee,
    payoneerFee: state.secondSection.totalFeeDetail.payoneerFee,
    fspFee: state.secondSection.totalFeeDetail.fspFee,
    pgFeeRate: state.firstSection.pgFeeRate,
    salesFeeRate: state.firstSection.salesFeeRate,
    payoneerFeeRate: state.firstSection.payoneerFeeRate,
    fspFeeRate: state.firstSection.fspFeeRate,
    totalInLocalCurrency: state.secondSection.totalInLocalCurrency
});

const SecondSectionContainer = connect(
    mapStateToProps
)(PresentationalSecondSection);

export default SecondSectionContainer;