import PresentationalFirstSection from '../components/PresentationalFirstSection';
import * as actions from '../actions';
import { connect } from 'react-redux';


// store 안의 state 값을 props 로 연결해줍니다.
const mapStateToProps = (state) => ({
    localCurrency: state.config.localCurrency,
    currencyRate: state.firstSection.currencyRate,
    productPriceInKrw: state.firstSection.productPriceInKrw,
    additionalCost: state.firstSection.additionalCost,
    productWeight: state.firstSection.productWeight,
    pgFeeRate: state.firstSection.pgFeeRate,
    salesFeeRate: state.firstSection.salesFeeRate,
    payoneerFeeRate: state.firstSection.payoneerFeeRate,
    fspFeeRate: state.firstSection.fspFeeRate
});

const mapDispatchToProps = (dispatch) => ({
    onChangeCountryCode: (e) => dispatch(actions.onChangeCountyCode(e.target.value)),
    onChangeCurrencyRate: (e) => dispatch(actions.onChangeCurrencyRate(e.target.value)),
    onChangeProductPriceInKrw: (e) => dispatch(actions.onChangeProductPriceInKrw(e.target.value)),
    onChangeAdditionalCost: (e) => dispatch(actions.onChangeAdditionalCost(e.target.value)),
    onChangeProductWeight: (e) => dispatch(actions.onChangeProductWeight(e.target.value)),
    onChangePgFeeRate: (e) => dispatch(actions.onChangePgFeeRate(e.target.value)),
    onChangeSalesFeeRate: (e) => dispatch(actions.onChangeSalesFeeRate(e.target.value)),
    onChangePayoneerFeeRate: (e) => dispatch(actions.onChangePayoneerFeeRate(e.target.value)),
    onChangeFspFeeRate: (e) => dispatch(actions.onChangeFspFeeRate(e.target.value)),
    onCalculatePriceForIncome: () => dispatch(actions.calculatePriceForIncome())
});

const FirstSectionContainer = connect(
    mapStateToProps,
    mapDispatchToProps
)(PresentationalFirstSection);

export default FirstSectionContainer;