import React, { useState } from 'react';

const PresentationalSecondSection = ({
    localCurrency,
    productPriceInLocalCurrency,
    additionalCostInLocalCurrency,
    shippingCostInLocalCurrency,
    costBeforeRebate,
    payByBuyer,
    payByShopee,
    rebate,
    totalFeeInLocalCurrency,
    pgFee,
    salesFee,
    payoneerFee,
    fspFee,
    pgFeeRate,
    salesFeeRate,
    payoneerFeeRate,
    fspFeeRate,
    totalInLocalCurrency
}) => {

    const [isOverseaPostCostCollapsed, setIsOverseaPostCostCollapsed] = useState(true);
    const toggleOverseaPostCostCollapse = () => setIsOverseaPostCostCollapsed(!isOverseaPostCostCollapsed);

    const [isFeeSummaryCollapsed, setIsFeeSummaryCollapsed] = useState(true);
    const toggleFeeSummaryCollapse = () => setIsFeeSummaryCollapsed(!isFeeSummaryCollapsed);

    return (
        <section className="page-section masthead portfolio" id="productInfoSection">
            <div className="container">
                <h2 className="page-section-heading text-uppercase text-secondary mb-0">2. 판매가격 측정</h2>
                <div className="divider-custom">
                    <div className="divider-custom-line" style={{ maxWidth: "initial" }}></div>
                </div>
                <h3 className="text-secondary content-in-container">배송 및 정산비용 내역</h3>
                <div className="row justify-content-between">
                    <div className="col-12 content-in-container invoice-content">
                        <div className="mt-3 table-responsive">
                            <table className="table table-invoice">
                                <thead>
                                    <tr>
                                        <th>항목</th>
                                        <th className="text-right" width="30%">비용</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr>
                                        <td>
                                            <span className="text-inverse">상품 가격</span><br />
                                            <small>원화 상품 가격에 환율을 적용한 순수 상품의 가격</small>
                                        </td>
                                        <td className="text-right">{localCurrency} {productPriceInLocalCurrency}</td>
                                    </tr>
                                    <tr>
                                        <td> <span className="text-inverse">기타 추가비용</span><br />
                                            <small>기타 추가 비용에 환율을 적용한 가격</small></td>
                                        <td className="text-right">{localCurrency} {additionalCostInLocalCurrency}</td>
                                    </tr>
                                    <tr className="accordion-toggle collapsed" aria-expanded="false" onClick={toggleOverseaPostCostCollapse}>
                                        <td>
                                            <span className="text-inverse">해외배송비</span><br />
                                            <small>감면액 적용 전 해외배송비 - 감면액</small>
                                        </td>
                                        <td className="text-right">{localCurrency}  {shippingCostInLocalCurrency}</td>
                                    </tr>
                                    <tr className="p">
                                        <td colSpan="6" className="hiddenRow">
                                            <div className={`${isOverseaPostCostCollapsed ? '' : 'show'} accordian-body collapse text-right p-3`} id="oversea-postfee">
                                                <p>해외배송비(감면액 적용 전) : <span>{localCurrency} {costBeforeRebate}</span></p>
                                                <p>고객 배송비 : <span>{localCurrency} {payByBuyer}</span> </p>
                                                <p>쇼피 리베이트 : <span>{localCurrency} {payByShopee}</span> </p>
                                                <p>배송비 감면액 : <span>{localCurrency} {rebate}</span> </p>
                                            </div>
                                        </td>
                                    </tr>
                                    <tr className="accordion-toggle collapsed" aria-expanded="false" onClick={toggleFeeSummaryCollapse}>
                                        <td>
                                            <span className="text-inverse">수수료 정산</span><br />
                                            <small>PG수수료 + 판매수수료 + 페이오니아 수수료 + FSP수수료</small>
                                        </td>
                                        <td className="text-right">{localCurrency} {totalFeeInLocalCurrency}</td>
                                    </tr>
                                    <tr className="p">
                                        <td colSpan="6" className="hiddenRow">
                                            <div className={`${isFeeSummaryCollapsed ? '' : 'show'} accordian-body collapse text-right p-3`} id="fee-summary">
                                                <p>해외 PG수수료(Transaction Fee) - {pgFeeRate}%: <span>{localCurrency} {pgFee}</span></p>
                                                <p>쇼피 판매수수료 - {salesFeeRate}% : <span>{localCurrency} {salesFee}</span> </p>
                                                <p>페이오니아 수수료 - {payoneerFeeRate}% : <span>{localCurrency} {payoneerFee}</span> </p>
                                                <p>FSP수수료 - {fspFeeRate}% : <span>{localCurrency} {fspFee}</span> </p>
                                            </div>
                                        </td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                        <div className="row justify-content-between invoice-price">
                            <div className="col-8 invoice-price-left">
                                <div className="row invoice-price-row">
                                    <div className="sub-price d-none d-md-block ">
                                        <small>상품가격</small>
                                        <span className="text-inverse">{localCurrency} {productPriceInLocalCurrency + additionalCostInLocalCurrency}</span>
                                    </div>
                                    <div className="sub-price d-none d-md-block ">
                                        <i className="fa fa-plus text-muted"></i>
                                    </div>
                                    <div className="sub-price d-none d-md-block ">
                                        <small>배송비</small>
                                        <span className="text-inverse">{localCurrency} {shippingCostInLocalCurrency}</span>
                                    </div>
                                    <div className="sub-price d-none d-md-block ">
                                        <i className="fa fa-plus text-muted"></i>
                                    </div>
                                    <div className="sub-price d-none d-md-block ">
                                        <small>수수료</small>
                                        <span className="text-inverse">{localCurrency} {totalFeeInLocalCurrency}</span>
                                    </div>
                                </div>
                            </div>
                            <div className="col-4 invoice-price-right">
                                <small>합계</small>
                                <span className="f-w-600">{localCurrency} {totalInLocalCurrency}</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default PresentationalSecondSection;




