import React from 'react';
import { OverlayTrigger, Tooltip } from 'react-bootstrap';

const PresentationalThirdSection = ({
    localCurrency,
    priceForIncome,
    marginRate,
    marginRatePercentage,
    isMinusMargin,
    margin,
    discountRate,
    discountRatePercentage,
    priceForRegistration,
    onChangeProceForIncome,
    onChangeDiscountRate
}) => {
    return (
        <section className="page-section masthead portfolio" id="marginInfoSection">
            <div className="container">
                <h2 className="page-section-heading text-uppercase text-secondary mb-0">3. 마진 및 가격 설정</h2>
                <div className="divider-custom">
                    <div className="divider-custom-line" style={{ maxWidth: "initial" }}></div>
                </div>
                <h3 className="text-secondary content-in-container">판매 가격</h3>
                <div className="row justify-content-between">
                    <div className="col-12 pr-6 content-in-container">
                        <div className="form-group">
                            <label htmlFor="sellingPrice">희망 판매 가격</label>
                            <OverlayTrigger
                                key="productWeight-info"
                                placement="top"
                                overlay={
                                    <Tooltip id="productWeight-info">
                                        상품을 팔고자하는 실제 가격. 희망 판매 가격을 수정하면 마진율과 마진금액을 확인 할 수 있음.
                                    </Tooltip>
                                }
                            >
                                <button type="button" className="btn btn-outline-secondary btn-circle btn-sm"><i className="fas fa-info"></i></button>
                            </OverlayTrigger>
                            <div className="input-group mb-3">
                                <input defaultValue={priceForIncome} onChange={onChangeProceForIncome} type="number" min="0" step="10" className="form-control" placeholder="실제 판매하고자 하는 가격을 입력하세요." aria-label="실제 판매하고자 하는 가격을 입력하세요." aria-describedby="sellingPrice-addon" id="sellingPrice" />
                                <div className="input-group-append">
                                    <span className="input-group-text" id="sellingPrice-addon">{localCurrency}</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-6 col-md-12 content-in-container">
                        <div className="form-group">
                            <label htmlFor="marginRate">마진율</label>
                            <OverlayTrigger
                                key="productWeight-info"
                                placement="top"
                                overlay={
                                    <Tooltip id="productWeight-info">
                                        상품 비용 및 수수료 대비 판매자가 얻는 마진율
                                    </Tooltip>
                                }
                            >
                                <button type="button" className="btn btn-outline-secondary btn-circle btn-sm"><i className="fas fa-info"></i></button>
                            </OverlayTrigger>
                            <div className="input-group mb-3">
                                <input defaultValue={marginRatePercentage} value={marginRatePercentage} type="number" className={`${isMinusMargin ? 'danger' : ''} form-control`} aria-describedby="marginRate-addon" id="marginRate" readOnly />
                                <div className="input-group-append">
                                    <span className="input-group-text" id="marginRate-addon">%</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-5 col-md-12 content-in-container">
                        <div className="form-group">
                            <label htmlFor="marginKrw">마진</label>
                            <OverlayTrigger
                                key="productWeight-info"
                                placement="top"
                                overlay={
                                    <Tooltip id="productWeight-info">
                                        상품 비용 및 수수료 대비 판매자가 얻는 마진 금액
                                    </Tooltip>
                                }
                            >
                                <button type="button" className="btn btn-outline-secondary btn-circle btn-sm"><i className="fas fa-info"></i></button>
                            </OverlayTrigger>
                            <div className="input-group mb-3">
                                <input defaultValue={margin} value={margin} type="number" className={`${isMinusMargin ? 'danger' : ''} form-control`} aria-describedby="marginKrw-addon" id="marginKrw" readOnly />
                                <div className="input-group-append">
                                    <span className="input-group-text" id="marginKrw-addon">{localCurrency}</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <h3 className="mt-5 text-secondary content-in-container">등록 가격</h3>
                <div className="row justify-content-between">
                    <div className="col-lg-6 col-md-12 content-in-container">
                        <div className="form-group">
                            <label htmlFor="discountPromotionRate">할인율</label>
                            <OverlayTrigger
                                key="productWeight-info"
                                placement="top"
                                overlay={
                                    <Tooltip id="productWeight-info">
                                        쇼피 할인 프로모션에 설정할 할인율
                                    </Tooltip>
                                }
                            >
                                <button type="button" className="btn btn-outline-secondary btn-circle btn-sm"><i className="fas fa-info"></i></button>
                            </OverlayTrigger>
                            <div className="input-group mb-3">
                                <input defaultValue={discountRatePercentage} onChange={onChangeDiscountRate} type="number" step="1" className="form-control" placeholder="쇼피에서 적용할 할인율을 입력하세요." aria-label="쇼피에서 적용할 할인율을 입력하세요." aria-describedby="discountPromotionRate-addon" id="discountPromotionRate" />
                                <div className="input-group-append">
                                    <span className="input-group-text" id="discountPromotionRate-addon">%</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-5 col-md-12 content-in-container">
                        <div className="form-group">
                            <label htmlFor="registeredPrice">등록 가격</label>
                            <OverlayTrigger
                                key="productWeight-info"
                                placement="top"
                                overlay={
                                    <Tooltip id="productWeight-info">
                                        쇼피 할인 프로모션에 할인율을 적용하여 희망 판매 가격을 받기 위한 상품 등록 가격
                                    </Tooltip>
                                }
                            >
                                <button type="button" className="btn btn-outline-secondary btn-circle btn-sm"><i className="fas fa-info"></i></button>
                            </OverlayTrigger>
                            <div className="input-group mb-3">
                                <input defaultValue={priceForRegistration} value={priceForRegistration} type="number" min="0" step="0.01" className="form-control" placeholder="할인율 적용전 상품 등록가격" aria-label="할인율 적용전 상품 등록가격" aria-describedby="registeredPrice-addon" id="registeredPrice" readOnly />
                                <div className="input-group-append">
                                    <span className="input-group-text" id="registeredPrice-addon">{localCurrency}</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default PresentationalThirdSection;




